/*
 * 1.0 Reset
 *****************************************************************************************************************************/
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*
 * 1.0 Variables
 *****************************************************************************************************************************/
/* Colors */
$charcoal: #353535;
$sharkskin: #666;
$acid: #446669;
$faint: #ddd;
$blue: #232D38;
$green: #2F4A4A;
$violet: #373A57;
$light-blue: lighten($blue, 15%);
$light-violet: lighten($violet, 15%);
$light-green: lighten($green, 15%);

/* Fonts */
$oswald: 'Oswald', serif;
$raleway: 'Raleway', sans-serif;

$font-weight-normal: 400;

$heading-color: $charcoal;
$heading-font-family: $oswald;
$heading-font-weight: $font-weight-normal;
$heading-line-height: 1.4;

$body-color: $charcoal;
$body-font-family: $raleway;
$body-font-size: 1.8rem;
$body-font-weight: $font-weight-normal;
$body-line-height: 1.6;

$link-color: $blue;

/* Grid */
$grid-columns: 12;
$grid-gutter: 1.5rem;

/*
 * 1.1 Mixins
 *****************************************************************************************************************************/
/* Animations */
@mixin down-up($duration: 2.5s) {
    animation: move-down $duration ease-in-out infinite;
}